export default {
	data() {
		return {
			br:'\n',
			orderId:0,
			uid:'',
			title:'',
			apt_site:'',
			apt_date:'',
			
			orderData:[],
			
			
			
		}
	},
	
	onShow:function(){
		let userinfo = this.getUserInfo();
		this.uid = userinfo.id;
		
		//获取订单
		this.getOrderByUid(this.uid);
	},
	
	onPullDownRefresh:function(){
		this.getOrderByUid(this.uid);
		setTimeout(function(){
			uni.stopPullDownRefresh();
		}, 1000);
	},
	
	methods:{
		getOrderByUid:function(uid){
			uni.request({
				url: this.createApiUrl('order/get/uid'),
				method: 'GET',
				data: {uid: this.uid},
				success: res => {
					res = res.data.data;
					console.log(res);
					this.orderData = res;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//删除前确认
		confirmDelete:function(orderId){
			uni.showModal({
				title: '删除作品',
				content: '您是否要删除作品？',
				showCancel: true,
				cancelText: '算了',
				confirmText: '是的',
				success: res => {
					if(res.confirm){
						console.log(orderId);
						//this.deleteWorks(orderId);
					}else if(res.cancel){
						console.log('用户点击取消');
					}
					
				},
				fail: () => {},
				complete: () => {
					uni.startPullDownRefresh({
						success() {
							setTimeout(function(){
								uni.stopPullDownRefresh();
							}, 1000);
						}
					})
				}
			});
		},
		
		//删除订单
		deleteWorks:function(orderId){
			console.log(orderId);
			uni.request({
				url: this.createApiUrl('order/delete'),
				method: 'POST',
				header: {
				        'content-type': 'application/x-www-form-urlencoded'  
				},
				data: {
					orderId: orderId
				},
				success: res => {
					console.log(res);
					if(res.data.code == 200){
						uni.showToast({
							title:'删除成功'
						})
					}else{
						uni.showToast({
							title:'删除失败'
						})
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
	}
}
